	addUserNotesWithFile(data,image:File): Observable<any> {
		if (data) {
				//data.file = image;
				
				const headers = new HttpHeaders();
				 headers.append('Content-Type', 'multipart/form-data');
				headers.append('Accept', 'application/json');
			
			
			var formData = new FormData();
			
			formData.append('notes',  data.notes);
			formData.append('added_by',  data.added_by);
			formData.append('added_for',  data.added_for);
			if(image !== undefined){
			
				formData.append('file',  image);	
			} 
			console.log(data); 
			return this.http.post(ElesAppConst.ADD_USER_NOTES, formData,{headers:headers});
		} else {
			return Observable.create((observer: Observer<any>) => { return observer.error(StringConst.INVALID_INFORMATION); })
		}
	}
	
